.footer {
    padding-top: 86px;
    padding-bottom: 105px;
    &-logo-wrap {
        grid-column: 1/4;
        img {
            margin-bottom: 24px;
            height: 42px;
        }
        .line {
            display: none;
        }
    }
    &-links-wrap {
        grid-column: 6/8;
        display: flex;
        flex-direction: column;
        a.footer-link {
            margin-bottom: 12px;
            @include text(1.2rem, bs, 2.2rem, $black-cl, uppercase);
            @include transition(color $t ease);
            &.active,
            &:hover {
                color: $blue-cl;
            }
        }
    }
    &-contact-wrap {
        grid-column: 9/13;
        margin-bottom: 52px;
        .contact-address {
            color: $black90-cl;
            margin-bottom: 20px;
            span {
                font-family: bs;
            }
        }
        .contact-phone,
        .contact-mail {
            margin-bottom: 20px;
            p.--text-12 {
                display: block;
            }
            a.text-link {
                color: $black-cl;
            }
        }
    }
    &-copy-wrap {
        grid-column: 6/13;
        grid-row: 2/3;
        @include col(7);
        column-gap: 32px;
        .line {
            grid-column: 1/8;
            grid-row: 1/2;
            margin-bottom: 29px;
        }
        .copy {
            grid-column: 1/4;
            color: $black70-cl;
        }
        .privacy-wrap {
            grid-column: 4/6;
            a.text-link {
                grid-column: 4/6;
                color: $black70-cl;
                @include transition($t);
                display: block;
                width: 84px;
                &:hover {
                    color: $black-cl;
                }
            }
        }
        
        .lang-wrap {
            grid-column: 6/8;
            display: flex;
            justify-content: flex-end;
            .lang-eng {
                margin-right: 25px;
            }
            .lang-eng,
            .lang-vie {
                color: $black70-cl;
                &.active,
                &:hover {
                    color: $blue-cl;
                }
            }
        }
    }
    @include sm {
        //max screen 992px
        padding-top: 60px;
        padding-bottom: 60px;
        &-logo-wrap {
            grid-column: 1/7;
            @include col(6);
            column-gap: 20px;
            grid-row: 1/2;            
            img {
                margin-bottom: 0;
                height: 42px;
                grid-column: 1/4;
            }
            .--h4 {
                grid-column: 4/7;
                font-size: 2rem;
                line-height: 3rem;
                margin-bottom: 42px;
            }
            .line {
                display: block;
                grid-column: 1/7;
            }
        }
        &-links-wrap {
            grid-column: 1/4;
            grid-row: 2/3;
            display: flex;
            flex-direction: column;
            margin-top: 38px;
            a.footer-link {
                margin-bottom: 12px;
            }
        }
        &-contact-wrap {
            grid-column: 4/7;
            grid-row: 2/3;
            margin-top: 38px;
            margin-bottom: 46px;            
        }
        &-copy-wrap {
            grid-column: 1/7;
            grid-row: 3/4;
            @include col(6);
            column-gap: 20px;
            .line {
                grid-column: 1/7;
                grid-row: 1/2;
            }  
            .copy {
                grid-column: 1/4;
            }
            .privacy-wrap {
                grid-column: 4/5;
                a.text-link {
                    grid-column: 4/5;
                }
            }            
            .lang-wrap {
                grid-column: 5/7;
            }          
        }
    }
    @include xs {
        //max screen 768px
        padding-bottom: 44px;
        &-logo-wrap {
            grid-column: 1/5;
            display: flex;
            flex-direction: column;
            column-gap: 20px;
            grid-row: 1/2;            
            img {
                margin-bottom: 0;
                height: 42px;            
                align-self: center;
                margin-bottom: 18px;
            }
            .--h4 {
                text-align: center;
                margin-bottom: 20px;
                margin-left: 32px;
                margin-right: 32px;
            }
            .line {
                margin-left: 32px;
                margin-right: 32px;
            }
        }
        &-links-wrap {
            grid-column: 1/5;
            grid-row: 2/3;
            flex-direction: row;
            text-align: center;
            justify-content: center;
            margin-top: 38px;
            a.footer-link {
                margin-bottom: 0px;
                margin-left: 12px;
                margin-right: 12px;
            }
        }
        &-contact-wrap {
            grid-column: 1/5;
            grid-row: 3/4;
            margin-top: 32px;
            margin-bottom: 12px;            
            margin-right: 32px;
            margin-left: 32px;
            display: flex;
            flex-direction: column;
            text-align: center;
            .--text-14 {
                text-align: center;
                span {
                    display: none;
                }
            }
        }
        &-copy-wrap {
            grid-column: 1/5;
            grid-row: 5/6;
            display: flex;
            flex-direction: column;
            align-items: center;
            .line {
                width: calc(100% - 64px);
                margin-left: 32px;
                margin-right: 32px;
            }  
            .copy {
                margin-bottom: 12px;
            }
            .privacy-wrap {
                grid-column: 1/5;
                margin-bottom: 12px;
                a.text-link {
                    grid-column: 1/5;
                }
            }            
            .lang-wrap {
                grid-column: 1/5;
            }          
        } 
    }     
}