.scheroabout {
    margin-top: -1px;
    padding: 25px 0 0;
    background-color: $blue-cl;
    position: relative;        
    .--h1 {
        grid-column: 2/7;
        grid-row: 1/2;
        color: $white-cl;
        margin-bottom: 83px;
    }
    .title-img-wrap {
        grid-column: 2/7;
        grid-row: 2/4;
        margin-right: -32px;
        //height: 537px;
        //margin-bottom: -253px;
        position: relative;
        overflow: hidden;
        img {
            @include midimg(100%);
        }
    }
    .content-main-wrap {
        grid-column: 8/13;
        grid-row: 1/3;
        margin-top: 94px;
        margin-bottom: 106px;
        .--text-20 {
            margin-bottom: 24px;
            color: $white-cl
        }
        .--text-16 {
            color: $white70-cl;
        }
    }
    .content-static-wrap {
        grid-column: 8/13;
        grid-row: 3/4;
        padding-top: 49px;
        @include col(5);
        column-gap: 32px;
        z-index: 1;
        .static-item {
            grid-column: span 2;
            margin-bottom: 42px;
            .--h3 {
                color: $green-cl;
                display: block;
            }
            .--text-14 {
                display: block;
            }
            &:nth-child(3),
            &:nth-child(4) {
                margin-bottom: 0;
            }
        }
    }
    &-bg-wrap {
        position: absolute;
        bottom: -1px;
        left: 0;
        right: 0;
        height: 252px;
        background-color: $white-cl;
        z-index: 0;
    }
    @include sm { //992px
        padding: 140px 0 0;      
        .--h1 {
            grid-column: 1/4;
            grid-row: 1/2;
            margin-bottom: 80px;
        }
        .title-img-wrap {
            grid-column: 1/4;
            margin-right: 0px;
            margin-right: 20px;
        }
        .content-main-wrap {
            grid-column: 4/7;
            margin-top: 60px;
            margin-bottom: 60px;
        }
        .content-static-wrap {
            grid-column: 4/7;
            padding-top: 40px;
            @include col(4);
            column-gap: 20px;
        }
    }
    @include xs { //768px
        padding: 150px 0 0;      
        .--h1 {
            grid-column: 1/5;
            grid-row: 2/3;
            margin-bottom: 32px;
        }
        .title-img-wrap {
            grid-column: 1/5;
            grid-row: 1/2;
            height: 320px;
            margin-right: 0px;
            margin-right: 0px;
            margin-bottom: 60px;
        }
        .content-main-wrap {
            grid-column: 1/5;
            grid-row: 3/4;
            margin-top: 0px;
            margin-bottom: 60px;
        }
        .content-static-wrap {
            grid-column: 1/5;
            grid-row: 4/5;
            padding-top: 40px;
            column-gap: 16px;
            .static-item {
                margin-bottom: 32px;
            }
        }
    }
}
.sccontentabout {
    padding: 128px 0 0;
    .line {
        grid-column: 2/13;
    }
    &-vistion {
        grid-column: 2/12;
        @include col(10);
        column-gap: 32px;
        margin-top: 130px;
        margin-bottom: 158px;
        .--h3 {
            grid-column: 1/11;
            margin-bottom: 32px;
        }
        .--text-20 {
            grid-column: 1/6;
        }
        .--text-14 {
            grid-column: 7/11;
        }
    }
    &-mission {
        grid-column: 1/12;
        @include col(11);
        column-gap: 32px;
        margin-bottom: 164px;
        .mission-img-wrap {
            grid-column: 1/7;
            margin-left: -136px;
            position: relative;
            overflow: hidden;
            .mission-img-main {
                @include overlay;
                .img-main {
                    @include midimg(100%);
                }
            }
            .img-bg {
                position: absolute;
                top: -307px;
                left: -270px;
            }
        }
        .mission-content {
            grid-column: 8/12;
            margin-top: 79px;
            margin-bottom: 72px;
            .--h3 {
                margin-bottom: 12px;
            }
            .--text-20 {
                margin-bottom: 12px;
            }
        }        
    }
    &-culture {
        grid-column: 2/12;
        @include col(10);
        column-gap: 32px;
        margin-bottom: 200px;
        .culture-content {
            grid-column: 1/5;
            margin-top: 23px;
            margin-bottom: 96px;
            .--h3 {
                margin-bottom: 12px;
            }
            .--text-20 {
                margin-bottom: 12px;
            }
        }
        .culture-img-wrap {
            grid-column: 7/11;
            position: relative;
            overflow: hidden;
            .culture-img-main {
                @include overlay;
                .img-main {
                    @include midimg(100%);
                }
            }  
            .img-bg {
                position: absolute;
                left: -90px;
                bottom: -90px;
            }
        }
    }
    @include sm {
        padding: 100px 0 0;
        .line {
            grid-column: 1/7;
        }
        &-vistion {
            grid-column: 1/7;
            @include col(6);
            column-gap: 20px;
            margin-top: 100px;
            margin-bottom: 140px;
            .--h3 {
                grid-column: 1/7;
            }
            .--text-20 {
                grid-column: 1/4;
            }
            .--text-14 {
                grid-column: 4/7;
            }
        }
        &-mission {
            grid-column: 1/7;
            @include col(6);
            column-gap: 20px;
            margin-bottom: 140px;
            .mission-img-wrap {
                grid-column: 1/4;
                margin-left: -32px;
                margin-right: 32px;
                .img-bg {
                    top: -187px;
                    left: -170px;
                }
            }
            .mission-content {
                grid-column: 4/7;
                margin-top: 60px;
                margin-bottom: 60px;
            }        
        }
        &-culture {
            grid-column: 1/7;
            @include col(6);
            column-gap: 20px;
            margin-bottom: 140px;
            .culture-content {
                grid-column: 1/4;
                margin-top: 40px;
                margin-bottom: 60px;
            }
            .culture-img-wrap {
                grid-column: 4/7;
                position: relative;
                .img-bg {
                    position: absolute;
                    left: -70px;
                    bottom: -70px;
                }
            }
        }
    }
    @include xs { //768px 
        padding: 80px 0 0;
        .line {
            grid-column: 1/5;
        }
        &-vistion {
            grid-column: 1/5;
            @include col(4);
            column-gap: 16px;
            margin-top: 80px;
            margin-bottom: 80px;
            .--h3 {
                grid-column: 1/5;
            }
            .--text-20 {
                grid-column: 1/5;
                margin-bottom: 24px;
            }
            .--text-14 {
                grid-column: 1/5;
            }
        }
        &-mission {
            grid-column: 1/5;
            @include col(4);
            column-gap: 16px;
            margin-bottom: 80px;
            .mission-img-wrap {
                grid-column: 1/5;
                margin-left: 0px;
                margin-right: 0px;
                height: 320px;                
                .img-bg {
                    display: none;
                }
            }
            .mission-content {
                grid-column: 1/5;
                margin-top: 60px;
                margin-bottom: 0;
            }        
        }
        &-culture {
            grid-column: 1/5;
            @include col(4);
            column-gap: 16px;
            margin-bottom: 80px;
            .culture-content {
                grid-column: 1/5;
                margin-top: 0px;
                margin-bottom: 0px;
                grid-row: 2/3;
            }
            .culture-img-wrap {
                grid-column: 1/5;
                grid-row: 1/2;
                height: 320px;
                margin-bottom: 60px;
                .img-bg {
                    display: none;
                }
            }
        }
    }
}
.scbusinessabout {
    padding-top: 130px;
    padding-bottom: 148px;
    background-color: $blue05-cl;
    &-title-wrap {
        grid-column: 1/5;
        .--h3 {
            margin-bottom: 24px;
        }
    }
    &-content {
        grid-column: 6/13;
        .business-item {
            @include col(7);
            column-gap: 32px;
            padding-top: 22px;
            cursor: pointer;            
            .--h5 {
                grid-column: 1/3;                
            }
            .business-item-content {
                grid-column: 3/8;                
                .title-wrap { 
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    .--text-16 {
                        color: $blue-cl;
                    }
                    .ic-plus {
                        @include size(12px);
                        position: relative;
                        .ic-plus-1,
                        .ic-plus-2 {
                            @include size(12px, 2px);
                            background-color: #A1B3C2;
                            position: absolute;
                            top: 7px;
                            left: 0;
                        }
                        .ic-plus-2 {
                            transform-origin: 50% 50%;
                            @include transform(rotate(90deg));
                            @include transition(all 0.5s ease); 
                        }
                    }
                }  
                .expand {
                    padding-top: 14px;
                    max-height: none;
                    overflow: hidden;
                    max-height: 0;
                    display: block;
                    @include transition(all 0.5s ease);
                }              
            }
            .line {
                grid-column: 1/8;
                margin-top: 6px;
                @include transition(all 0.5s ease);
                background-color: $blue10-cl;
            }
            &.active {
                .business-item-content {
                    .title-wrap {
                        .ic-plus {
                            .ic-plus-2 {
                                @include transform(none);
                            }
                        }
                    }
                    .expand {
                        max-height: 200px;
                    }
                }
                .line {
                    margin-top: 20px;
                }
            }
        }
    }
    @include sm {
        padding-top: 100px;
        padding-bottom: 108px;
        &-title-wrap {
            grid-column: 1/6;
            margin-bottom: 60px;
            .--h2 {
                margin-bottom: 12px;
            }
        }
        &-content {
            grid-column: 1/7;
            .business-item {
                @include col(6);
                column-gap: 20px;
                padding-top: 22px;            
                .--h5 {
                    grid-column: 1/2;                
                }
                .business-item-content {
                    grid-column: 3/7;                                                                      
                }
                .line {
                    grid-column: 1/7;
                }
                &.active {
                    .business-item-content {
                        .expand {
                            max-height: 300px;
                        }
                    }
                }
            }
        } 
    }
    @include xs { //768px 
        adding-top: 80px;
        padding-bottom: 100px;
        &-title-wrap {
            grid-column: 1/5;
            margin-bottom: 60px;
            .--h2 {
                margin-bottom: 12px;
            }
        }
        &-content {
            grid-column: 1/5;
            .business-item {
                @include col(4);
                column-gap: 16px;
                padding-top: 22px;            
                .--h5 {
                    grid-column: 1/2;                
                }
                .business-item-content {
                    grid-column: 2/5;                                                                      
                }
                .line {
                    grid-column: 1/5;
                }                
            }
        } 
    }
}