.scherohome {
    padding: 67px 0 167px;
    overflow: hidden;
    &-content-wrap {
        grid-column: 2/8;
        .--text-14 {
            color: $black60-cl;
        }
        .--h1 {
            margin-bottom: 40px;
            margin-left: -5px;
        }
        p.--text-16 {
            margin-bottom: 20px;
            padding-right: 100px;          
        }
    }
    &-slide-container {
        grid-column: 9/13;
        position: relative;
        @include size(100%);
        .slide-bg-wrap {
            @include fullscreen(false);
            .bg-circle-solid {
                width: 96%;
                height: 0;
                padding-bottom: 96%;
                background-color: $green-cl;
                @include mid(false);
                @include br(50%);
                .bg-dotted-outer {
                    position: absolute;
                    width: 200%;
                    height: 200%;
                    @include mid(false);
                    img {
                        @include mid(false);
                    }
                }
                .bg-circle-blur {
                    position: absolute;
                    width: 180%;
                    height: 180%;
                    @include mid(false);
                    img {
                        @include mid(false);
                    }
                }
            }
        }
    }
    .swiper {
        @include size(100%);
        position: relative;
        .swiper-slide {
            @include mid(true);
            img {
                max-width: 100%;
            }
        }    
    }
    .swiper-pagination {
        bottom: -60px;
        height: 8px;
        left: 50%;
        @include transform(translateX(-50%));
        .swiper-pagination-bullet {
            background-color: #C4C4C4;
            @include size(8px);
            margin: 0 5px;
            @include transition($t);
            &:hover {
                background-color: $blue-cl;
            }
        }
        .swiper-pagination-bullet-active {
            background-color: $blue-cl;
        }
    }
    @include sm {
        //max screen 992px
        padding: 200px 0 160px;
        &-content-wrap {
            grid-column: 1/7;
            display: flex;
            flex-direction: column;
            text-align: center;
            margin-bottom: 80px;
            .--text-14 {
                text-align: center;                
            }
            .--h1 {
                margin-bottom: 20px;
                text-align: center;
                padding-left: 64px;
                padding-right: 64px;
            }
            p.--text-16 {
                text-align: center;
                margin-bottom: 20px;
                padding-left: 100px;
                padding-right: 100px;
            }
            .text-link {
                
                align-self: center;
            }
        }
        &-slide-container {
            grid-column: 2/6;
            height: 540px;
            .slide-bg-wrap {
                .bg-circle-solid {
                    width: 80%;
                    padding-bottom: 80%;
                    .bg-dotted-outer {
                        width: 160%;
                        height: 160%;
                    }
                    .bg-circle-blur {
                        width: 140%;
                        height: 140%;
                    }
                }                
            }
        }
        .swiper {
            .swiper-slide {
                img {
                    max-width: 70%;
                }
            }     
        }
        .swiper-pagination {
            bottom: -0px;        
        }
    }
    @include xs {
        //max screen 768px
        padding: 140px 0 80px;
        &-content-wrap {
            grid-column: 1/5;
            margin-bottom: 40px;
            .--text-14 {
                text-align: center;                
            }
            .--h1 {
                margin-bottom: 16px;
                padding-left: 0;
                padding-right: 0;
            }
            p.--text-16 {
                padding-left: 32px;
                padding-right: 32px;
            }
            .text-link {                    
                align-self: center;
            }
        }
        &-slide-container {
            grid-column: 1/5;
            height: 400px;
            .slide-bg-wrap {
                .bg-circle-solid {
                    width: 50%;
                    padding-bottom: 50%;
                }                
            }
        }
        .swiper {
            .swiper-slide {
                img {
                    max-height: 110%;
                }
            }     
        }
    }
    @include xxs {
        //max screen 576px
        &-content-wrap {
            margin-bottom: 30px;
            p.--text-16 {
                padding-left: 0px;
                padding-right: 0px;
            }
        }
        &-slide-container {
            .slide-bg-wrap {
                .bg-circle-solid {
                    width: 60%;
                    padding-bottom: 60%;
                }                
            }
        }
    }
}

.scabouthome {
    &-bg-wrap {
        width: 100%;
        height: 520px;
        overflow: hidden;        
        margin-bottom: 152px;
        .scabout-img-wrap {
            position: relative;
            @include size(100%);
            img {
                @include midimg(100%);
            }
        }        
    }
    .--h2 {
        grid-column: 2/5;
        margin-right: -32px;
    }
    &-content-wrap {
        grid-column: 7/13;
        .--text-20 {
            margin-bottom: 24px;
        }
        .--text-16 {
            margin-bottom: 15px;
            &:nth-last-child() {
                margin-bottom: 24px;
            }
        }
        margin-bottom: 117px;
    }
    .line {
        grid-column: 2/13;
    }
    @include sm {
        //max screen 992px
        &-bg-wrap {
            height: 420px;
            margin-bottom: 80px;
        }
        .--h2 {
            grid-column: 2/5;
            margin-right: 0px;
            margin-bottom: 40px;
        }
        &-content-wrap {
            grid-column: 2/7;
            .--text-20 {
                margin-bottom: 24px;
            }
            .text-link {
                margin-bottom: 0;
            }
            margin-bottom: 80px;
        }
        .line {
            grid-column: 2/7;
        }
    }
    @include xs {
        //max screen 768px
        &-bg-wrap {
            height: 320px;
            margin-bottom: 60px;
        }
        .--h2 {
            grid-column: 1/5;
            margin-right: 0px;
            margin-bottom: 20px;
        }
        &-content-wrap {
            grid-column: 1/5;
            margin-bottom: 80px;
        }
        .line {
            grid-column: 1/5;
        }
    }
}
.scproducthome {
    padding-top: 80px;
    padding-bottom: 132px;
    margin-bottom: 182px;
    overflow: hidden;
    &-title-wrap {
        grid-column: 2/11;
        .--text-14 {
            text-transform: uppercase;
            margin-bottom: 8px;
        }
        .--h2 {
            margin-bottom: 80px;
        }
    }
    &-product-con {
        grid-column: 2/11;
        grid-row: 2/3;
        @include size(100%);
        .product-wrap {
            .product-item-wrap {
                .item-img-wrap {
                    height: 294px;
                    background-color: $black05-cl;
                    margin-bottom: 24px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    img {
                        max-width: 50%;
                    }
                }
                .line {
                    background-color: $green-cl;
                    height: 3px;
                }
                .item-content-wrap {
                    margin-top: 12px;
                    .--h5 {
                        margin-bottom: 12px;
                    }
                    p.--text-14 {
                        margin-bottom: 12px;
                    }
                    
                }
            }
        }
        .swiper {
            @include size(100%);
        }
    }
    .swiper-product-nav {
        grid-column: 11/13;
        grid-row: 1/2;
        margin-top: auto;
        margin-bottom: 90px;
        .swiper-nav-wrap {
            display: flex;
            justify-content: flex-end;
            height: 44px;
            .swiper-btn-prev,
            .swiper-btn-next {
                @include size(44px);
                background-color: $black05-cl;
                @include mid(true);
                @include br(50%);
                margin-left: 16px;
                cursor: pointer;
                @include transition($t);
                svg path {
                    @include transition($t);
                }
                &:hover {
                    background-color: $green-cl;
                    svg path {
                        fill: #ffffff;
                        fill-opacity: 1;
                    }
                }
            }
        }
    }
    @include sm {
        //max screen 992px
        padding-top: 80px;
        padding-bottom: 140px;
        margin-bottom: 140px;
        &-title-wrap {
            grid-column: 2/7;
            grid-row: 1/2;
            .--h2 {
                margin-bottom: 24px;
            }
        }
        &-product-con {
            grid-column: 2/7;
            grid-row: 3/4;
            .product-wrap {
                .product-item-wrap {
                    .item-img-wrap {
                        img {
                            max-width: 45%;
                            max-height: 80%;
                        }
                    }
                }
            }
        }
        .swiper-product-nav {
            grid-column: 2/4;
            grid-row: 2/3;
            margin-bottom: 80px;    
            .swiper-nav-wrap {
                justify-content: flex-start;
                .swiper-btn-prev,
                .swiper-btn-next {                    
                    margin-left: 0px;
                    margin-right: 16px;                    
                }
            }        
        }
    }
    @include xs {
        //max screen 768px
        padding-top: 80px;
        padding-bottom: 80px;
        margin-bottom: 80px;
        &-title-wrap {
            grid-column: 1/5;
            grid-row: 1/2;
            .--h2 {
                margin-bottom: 24px;
            }
        }
        &-product-con {
            grid-column: 1/5;
            grid-row: 3/4;
            .product-wrap {
                .product-item-wrap {
                    .item-img-wrap {
                        height: 260px;
                        img {
                            max-width: 65%;
                        }
                    }
                }
            }
        }
        .swiper-product-nav {
            grid-column: 1/5;
            grid-row: 2/3;
            margin-bottom: 40px;            
        }
    }        
}
.scservicehome {
    margin-top: 182px;    
    padding-bottom: 128px;
    background-color: $blue-cl;
    .line {
        width: 100%;
        height: 10px;
        background-color: $green-cl;
    }
    &-bg-wrap {
        grid-column: 1/8;
        grid-row: 1/2;
        margin-left: -136px;
        height: 532px;
        margin-top: -192px;
        overflow: hidden;
        position: relative;
        img {
            @include midimg(100%);
        }
    }
    &-content-wrap {
        grid-column: 9/13;
        grid-row: 1/3;
        padding-top: 136px;        
        .--text-14 {
            text-transform: uppercase;
            color: $white-cl;
            margin-bottom: 8px;
        }
        .--h3 {
            color: $white-cl;
            margin-bottom: 64px;
        }
        p.--text-16 {
            color: $white70-cl;
            margin-bottom: 15px;
            &:nth-last-child() {
                margin-bottom: 23px;
            }
        }
        a.text-link {
            color: $green-cl;
        }
    }
    &-quote-wrap {
        grid-column: 2/8;
        grid-row: 2/3;
        display: flex;
        align-items: flex-start;
        img {
            margin-left: 33px;
            margin-right: 62px;
        }
        .--h5 {
            color: $white-cl;
        }
    }
    @include sm {
        //max screen 992px
        margin-top: 140px;
        padding-bottom: 140px;
        &-bg-wrap {
            grid-column: 1/6;
            grid-row: 1/2;
            margin-left: -32px;
            height: 420px;
            margin-top: -150px;
        }
        &-content-wrap {
            grid-column: 2/7;
            grid-row: 2/3;
            padding-top: 80px;
            margin-bottom: 64px;
            .--h3 {
                color: $white-cl;
                margin-bottom: 32px;
            }
        }
        &-quote-wrap {
            grid-column: 2/7;
            grid-row: 3/4;
            position: relative;
            img {
                position: absolute;
                left: -62px;
                top: 6px;
                width: 32px;
                margin-left: 0;
                margin-right: 0;
            }
        }
    }
    @include xs {
        //max screen 768px 
        margin-top: 80px;
        padding-bottom: 100px;
        &-bg-wrap {
            grid-column: 1/5;
            margin-left: -20px;
            height: 320px;
            margin-top: -90px;
        }
        &-content-wrap {
            grid-column: 1/5;
            padding-top: 60px;
            margin-bottom: 60px;
            .--h3 {
                color: $white-cl;
                margin-bottom: 20px;
            }
        }
        &-quote-wrap {
            grid-column: 1/5;
            margin-left: 62px;
            margin-right: 32px;
        }
    }
    @include xxs {
        //max screen 576px
        &-quote-wrap {
            margin-right: 12px;
        }
    }
}