.productpage {
    .header {
        background-color: $blue05-cl;
    }
}
.scheroproduct {
    margin-top: -1px;
    padding: 67px 0 185px;
    background-color: $blue05-cl;
    &-content {
        margin-top: 33px;
        margin-bottom: 34px;
        grid-column: 2/7;
        p.--text-14 {
            color: $black60-cl;
        }
        .--h1 {
            margin-bottom: 27px;
            margin-left: -5px;
            margin-right: 20px;
        }
        p.--text-16 {
            margin-bottom: 19px;
        }
    }
    &-img-wrap {
        grid-column: 8/12;
        position: relative;
        margin-left: 30px;
        margin-right: -10px;
        .img-bg-wrap {
            @include mid;
            width: 100%;
            height: 0px;
            padding-bottom: 100%;
            @include br(50%);
            background-color: $blue10-cl;
        }
        img {
            @include mid; 
            padding-top: 20px;
        }
    }
    @include sm {
        padding: 140px 0 100px;
        &-content {
            margin-top: 32px;
            margin-bottom: 34px;
            grid-column: 1/4;
            .--h1 {
                margin-bottom: 24px;
                margin-left: -5px;
                margin-right: -20px;
            }
            p.--text-16 {
                margin-bottom: 19px;
                margin-right: -20px;
            }
        }
        &-img-wrap {
            grid-column: 4/7;
            margin-left: 20px;
            margin-right: -10px;
            margin-top: 20px;
            .img-bg-wrap {
                width: 80%;
                padding-bottom: 80%;
            }
            img {
                max-width: 85%;
            }
        } 
    }
    @include xs {
        padding: 140px 0 80px;
        &-content {
            margin-top: 0px;
            margin-bottom: 40px;
            grid-column: 1/5;
            display: flex;
            flex-direction: column;
            align-items: center;
            .--h1 {
                margin-left: 0;
                margin-right: 0;
                text-align: center;
                font-size: 3.2rem;
                line-height: 4rem;
                margin-bottom: 10px;
            }
            p.--text-16 {
                margin-right: 0px;
                text-align: center;
            }
        }
        &-img-wrap {
            grid-column: 1/5;
            margin-left: 0px;
            margin-right: 0px;
            margin-top: 0px;
            height: 480px;
            .img-bg-wrap {
                width: 80%;
                padding-bottom: 80%;
            }
            img {
                max-width: 85%;
                max-height: 100%;
            }
        }
    }
}
.sccontentproduct {
    padding-top: 70px;
    &-title {
        grid-column: 1/5;
        .--h3 {
            margin-bottom: 53px;
        }
    }
    &-products {
        grid-column: 1/13;
        @include col(12);
        column-gap: 32px;
        .product-item {
            grid-column: span 4;
            margin-bottom: 60px;
            .item-img-wrap {
                height: 390px;
                background-color: $black05-cl;
                margin-bottom: 20px;
                display: flex;
                align-items: center;
                justify-content: center;
                img {
                    max-width: 45%;
                }
            }
            .item-content-wrap {
                .--h5 {
                    margin-bottom: 12px;
                }
                .--text-14 {
                    margin-bottom: 12px;
                }
            }
        }
    }
    .line {
        grid-column: 1/13;
        background-color: $black10-cl;
    }
    @include sm {
        padding-top: 60px;
        &-title {
            grid-column: 1/4;
            .--h3 {
                margin-bottom: 40px;
            }
        }
        &-products {
            grid-column: 1/7;
            @include col(6);
            column-gap: 20px;
            .product-item {
                grid-column: span 2;
                margin-bottom: 48px;
                .item-img-wrap {
                    height: 300px;
                    img {
                        max-width: 50%;
                        max-height: 90%;
                    }
                }
            }
        }
        .line {
            grid-column: 1/7;
        }
    }
    @include xs {
        &-title {
            grid-column: 1/5;
            .--h3 {
                margin-bottom: 32px;
            }
        }
        &-products {
            grid-column: 1/5;
            @include col(4);
            column-gap: 16px;
            .product-item {
                .item-img-wrap {
                    height: 280px;
                    img {
                        max-width: 45%;
                        max-height: 80%;
                    }
                }
            }
        }
        .line {
            grid-column: 1/5;
        }
    }
    @include xxs {
        &-products {
            .product-item {
                grid-column: span 4;
            }
        }
    }
}