.detailpage {
    .header {
        background-color: $blue05-cl;
    }
}
.scherodetail {
    margin-top: -1px;
    padding-top: 64px;
    background-color: $blue05-cl;
    position: relative;
    .container {
        position: relative;
        z-index: 1;
    }
    &-img-wrap {
        grid-column: 1/8;
        background-color: $white-cl;
        .img-main-wrap {
            width: 100%;
            height: 500px;
            margin-bottom: 16px;
            position: relative;
            .img-main {
                @include mid(false);
                @include size(100%);
                overflow: hidden;
                opacity: 0;
                pointer-events: none;
                @include transition(opacity $t ease);
                img {
                    position: absolute;
                    left: 50%;
                    @include transform(translateX(-50%));
                    max-width: 100%;
                    max-height: 100%;
                    height: 100%;
                }
                &.active {
                    pointer-events: auto;
                    opacity: 1;
                }
            }
        }
        .img-nav-wrap {
            @include col(5);
            column-gap: 16px;
            .img-nav {
                overflow: hidden;
                @include transition($t);
                border: 1px solid $black10-cl;
                cursor: pointer;                
                img {
                    pointer-events: none;
                }
                &.active,
                &:hover {
                    border: 1px solid $green-cl;
                }
            }
        }
    }
    &-content {
        grid-column: 9/13;
        margin-top: 63px;
        .content-breadcrumb {
            margin-bottom: 10px;
            a, span {
                color: $black60-cl;
            }
        }
        .--h3 {
            margin-bottom: 70px;
        }
        .content-descrip-wrap {
            z-index: 1;
            padding-top: 50px;
            .--text-20 {
                margin-bottom: 24px;
            }
        }
    }
    &-bg-wrap {
        position: absolute;
        bottom: -1px;
        left: 0;
        right: 0;
        height: 328px;
        background-color: $white-cl;
        z-index: 0;
    }
    @include sm {
        padding-top: 140px;
        &-img-wrap {
            grid-column: 1/4;
            margin-top: 40px;
            .img-main-wrap {
                height: 400px;
                .img-main {
                    @include size(90%);
                }
            }
        }
        &-content {
            grid-column: 4/7;
            margin-top: 0;
            .--h3 {
                margin-bottom: 32px;
            }
            .content-descrip-wrap {
                padding-top: 32px;
            }
        }
    }
    @include sm {
        padding-top: 140px;
        &-img-wrap {
            grid-column: 1/7;
            margin-top: 0px;
            margin-bottom: 40px;
            .img-main-wrap {
                height: 320px;
                .img-main {
                    @include size(100%);
                }
            }
        }
        &-content {
            grid-column: 1/7;
        }
    }
    @include xxs {
        &-img-wrap {
            .img-main-wrap {
                height: 280px;
            }
        }
    }
}
.scdescripdetail {
    padding-top: 120px;
    padding-bottom: 20px;
    .line-top {
        grid-column: 1/13;
        background-color: $black10-cl;
        margin-bottom: 70px;
    }
    .--h4 {
        grid-column: 1/3;
    }
    &-content {
        grid-column: 5/13;
        margin-bottom: 80px;
        ul {
            li {
                margin-bottom: 10px;
            }
        }
    }
    .line-mid {
        grid-column: 5/13;
        background-color: $black10-cl;
        margin-bottom: 80px;
    }
    @include sm {
        padding-top: 80px;
        padding-bottom: 40px;
        .line-top {
            grid-column: 1/7;
            margin-bottom: 60px;
        }
        .--h4 {
            grid-column: 1/3;
        }
        &-content {
            grid-column: 3/7;
            margin-bottom: 60px;
        }
        .line-mid {
            grid-column: 3/7;
            margin-bottom: 60px;
        }
    }
    @include xs {
        padding-top: 60px;
        padding-bottom: 32px;
        .line-top {
            grid-column: 1/7;
            margin-bottom: 40px;
        }
        .--h4 {
            grid-column: 1/7;
            margin-bottom: 24px;
        }
        &-content {
            grid-column: 1/7;
            margin-bottom: 32px;
        }
        .line-mid {
            grid-column: 1/7;
            margin-bottom: 32px;
        }
    }
}
.scotherdetail {
    padding-top: 80px;
    padding-bottom: 92px;
    background-color: $blue05-cl;
    &-title {
        grid-column: 1/13;
        display: flex;
        justify-content: space-between;
        margin-bottom: 42px;
        .swiper-product-nav {
            .swiper-nav-wrap {
                display: flex;
                justify-content: flex-end;
                height: 44px;
                .swiper-btn-prev,
                .swiper-btn-next {
                    @include size(44px);
                    background-color: $black05-cl;
                    @include mid(true);
                    @include br(50%);
                    margin-left: 16px;
                    cursor: pointer;
                    @include transition($t);
                    svg path {
                        @include transition($t);
                    }
                    &:hover {
                        background-color: $green-cl;
                        svg path {
                            fill: #ffffff;
                            fill-opacity: 1;
                        }
                    }
                }
            }
        }        
    }
    &-product-con {
        grid-column: 1/13;
        overflow: hidden;    
        .swiper-product {
            width: 100%;
            .swiper-wrapper {
                width: 100%;
                .product-item-wrap {
                    width: calc((100% - 64px) / 3);
                    .item-img-wrap {
                        height: 390px;
                        background-color: $white-cl;
                        margin-bottom: 20px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        img {
                            max-width: 45%;
                        }
                    }
                    .item-content-wrap {
                        .--h5 {
                            margin-bottom: 12px;
                        }
                        .--text-14 {
                            margin-bottom: 12px;
                        }
                    }
                }
            }
        }
    }
    @include sm {
        padding-top: 80px;
        padding-bottom: 92px;
        &-title {
            grid-column: 1/7;      
        }
        &-product-con {
            grid-column: 1/7;
            .swiper-product {
                .swiper-wrapper {
                    .product-item-wrap {
                        width: calc((100% - 40px) / 3);
                        .item-img-wrap {
                            height: 300px;
                            img {
                                max-width: 50%;
                                max-height: 90%;
                            }
                        }
                    }
                }
            }
        } 
    }
    @include sm {
        padding-top: 60px;
        padding-bottom: 72px;
        &-product-con {
            .swiper-product {
                .swiper-wrapper {
                    .product-item-wrap {
                        width: calc((100% - 32px) / 3);
                        .item-img-wrap {
                            height: 280px;
                            img {
                                max-width: 45%;
                                max-height: 80%;
                            }
                        }
                    }
                }
            }
        }
    }
    @include xxs {
        &-title {
            align-items: flex-start;
            flex-direction: column;
            margin-bottom: 32px;
            .--h4 {
                margin-bottom: 24px;
            }
            .swiper-product-nav {
                .swiper-nav-wrap {
                    justify-content: flex-start;
                    .swiper-btn-prev,
                    .swiper-btn-next {
                        margin-left: 0px;
                        margin-right: 16px;
                    }
                }
            }        
        }
        &-product-con {
            .swiper-product {
                .swiper-wrapper {
                    .product-item-wrap {
                        width: calc((100% - 32px) / 3);
                        .item-img-wrap {
                            height: 280px;
                            img {
                                max-width: 45%;
                                max-height: 80%;
                            }
                        }
                    }
                }
            }
        }
    }
}