.contactpage {
    .header {
        background-color: $blue05-cl;
    }
}
.scherocontact {
    margin-top: -1px;
    padding-top: 46px;
    padding-bottom: 131px;
    background-color: $blue05-cl;
    &-img-wrap {
        grid-column: 1/6;
        margin-right: -32px;
        position: relative;
        overflow: hidden;
        img {
            @include midimg(100%);
        }
    }
    &-content {
        grid-column: 7/13;
        margin-top: 9px;
        margin-bottom: 23px;
        @include col(6);
        column-gap: 32px;
        .--label {
            grid-column: 1/3;
            color: $black60-cl
        }
        .--h1 {
            grid-column: 1/6;
            margin-bottom: 39px;
        }
        .line {
            grid-column: 1/7;
            background-color: $black10-cl;
        }
        .contact-info-wrap {
            margin-top: 50px;
            margin-bottom: 50px;
            grid-column: span 3;
            .--title {
                color: $black90-cl;
                margin-bottom: 16px;
            }
            .--address {
                color: $black90-cl;
                margin-bottom: 10px;
            }
        }
        .contact-email-wrap {
            margin-top: 39px;
            grid-column: 1/7;
            .--text-14 {
                display: block;
                margin-bottom: 11px;
            }

        }
    }
    @include sm { //992px
        padding-top: 180px;
        padding-bottom: 100px;
        &-img-wrap {
            grid-column: 1/4;
            margin-right: 16px;
        }
        &-content {
            grid-column: 4/7;
            margin-bottom: 24px;
            @include col(4);
            column-gap: 16px;
            .--label {
                grid-column: 1/5;
            }
            .--h1 {
                grid-column: 1/5;
            }
            .line {
                grid-column: 1/5;
            }
            .contact-info-wrap {
                margin-top: 50px;
                margin-bottom: 50px;
                grid-column: span 2;
            }
            .contact-email-wrap {
                margin-top: 32px;
                grid-column: 1/5;
            }
        }
    }
    @include xs { //768px
        padding-top: 140px;
        padding-bottom: 80px;
        &-img-wrap {
            grid-column: 1/5;
            margin-right: 0px;
            height: 320px;
            margin-bottom: 60px;
        }
        &-content {
            grid-column: 1/5;
            margin-bottom: 24px;
            @include col(4);
            column-gap: 16px;
            .--h1 {
                margin-bottom: 0;
            }
            .contact-info-wrap {
                margin-top: 32px;
                margin-bottom: 0px;
                grid-column: 1/5;
            }
            .line {
                margin-top: 32px;
            }
        }
    }
}