.header {
    padding: 44px 0 32px;
    position: static;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10;
    .container {
        padding: 0 40px;
        .logo-wrap {
            grid-column: 1/2;
            margin-left: -96px;
            margin-top: 7px;
            .logo-style-1 {
                display: block;
            }
            .logo-style-2 {
                display: none;
            }
        }
        .header-links-wrap {
            grid-column: 2/6;
            @include col(2);
            column-gap: 32px;
            .header-link {
                @include text(1.2rem, bs, 2.2rem, $black-cl, uppercase);
                margin-bottom: 12px;
                display: inline-block;
                @include transition( color $t ease);
                &:hover,
                &.active {
                    color: $green-cl;
                }
            }
        }
        .header-contact-wrap {
            grid-column: 9/13;
            @include col(2);
            column-gap: 32px;
            .--text-12 {
                display: block;
            }
            .contact-link {
                display: inline-block;
            }
        }
        .line {
            grid-column: 2/13;
            opacity: 0;
            margin-top: 21px;
            pointer-events: none;
        }
    }
    &.style-2 {
        background-color: $blue-cl;
        .container {
            .logo-wrap {
                .logo-style-1 {
                    display: none;
                }
                .logo-style-2 {
                    display: block;
                }
            }
            .header-links-wrap {
                .header-link {
                    color: $white-cl;
                    &:hover,
                    &.active {
                        color: $green-cl;
                    }
                }
            }
            .header-contact-wrap {
                .--text-12 {
                    color: $white-cl;
                }
                .contact-link {
                    color: $white-cl;
                }
            }
            .line {
                background-color: $white10-cl;
                display: block;
                opacity: 1;
            }
        }
    }
    @include md {
        .container {
            padding: 0 40px;
            .logo-wrap {
                grid-column: 1/3;
                margin-left: 0;
            }
            .header-links-wrap {
                grid-column: 3/7;
            }        
        }
    }
    @include sm {
        display: none;
    } 
}
.header.sticky {
    position: fixed;
    pointer-events: none;
    padding: 16px 0 16px;
    background-color: white;
    box-shadow: 0 2px 4px rgba(0,0,0,.05);
    @include transform(translateY(-100%));
    @include transition(all $t ease);
    .container {
        pointer-events: auto; 
        .logo-wrap {
            margin-top: 0;
            grid-column: 1/2;
        }
        .header-links-wrap {
            grid-column: 2/9;
            display: flex;
            align-items: center;
            .top-line {
                display: none;
            }
            .header-link {
                margin-bottom: 0;
            }
            .line {
                display: none;
            }
            .contact-wrap {
                display: none;
            }
        }       
        .header-contact-wrap {
            .contact-wrap {
                display: flex;
                align-items: center;
                .contact-link {
                    font-size: 1.4rem;
                    line-height: 2.2rem;
                }
            }               
        }
    }
    &.show {
        @include transform(none);
    }
    &.style-2 {
        border-bottom: 1px solid $white10-cl; 
        .container {
            .header-links-wrap {
                .header-link {
                    color: $black-cl;
                    &:hover,
                    &.active {
                        color: $green-cl;
                    }
                }
                .line {
                    background-color: $black10-cl;
                }
            }
            .header-menu-toggle {
                .ic-menu-1,
                .ic-menu-2,
                .ic-menu-3 {
                    background-color: $white-cl;
                }
                &.show {
                    .ic-menu-1,
                    .ic-menu-2,
                    .ic-menu-3 {
                        background-color: $black90-cl;
                    }
                }
            }
        }
        &.scroll {
            border-bottom: 1px solid $black10-cl;
            .container {
                .logo-wrap {
                    .logo-style-1 {
                        display: block;
                    }
                    .logo-style-2 {
                        display: none;
                    }
                }
                .header-menu-toggle {
                    .ic-menu-1,
                    .ic-menu-2,
                    .ic-menu-3 {
                        background-color: $black90-cl;
                    }
                }
            }
        }
        &.toggle {
            .container {
                .logo-wrap {
                    .logo-style-1 {
                        display: block;
                    }
                    .logo-style-2 {
                        display: none;
                    }
                }                    
            }
        }
        &.show {
            .container {
                .logo-wrap {
                    .logo-style-1 {
                        display: block;
                    }
                    .logo-style-2 {
                        display: none;
                    }
                }
                .header-contact-wrap {
                    .contact-wrap {
                        .contact-link {
                            color: $black-cl;
                        }
                    }               
                }                  
            }
            @include sm {
                .container {
                    .logo-wrap {
                        .logo-style-1 {
                            display: none;
                        }
                        .logo-style-2 {
                            display: block;
                        }
                    }
                }
                &.scroll {
                    .logo-wrap {
                        .logo-style-1 {
                            display: block;
                        }
                        .logo-style-2 {
                            display: none;
                        }
                    }
                }
            }
        }
    }
    @include md {
        .container {
            .logo-wrap {
                grid-column: 1/3;
            }
            .header-links-wrap {
                grid-column: 3/9;
            }
        }
    }
    @include sm {
        padding: 24px 0;
        display: block;
        @include transform(translateY(-100%));
        background-color: transparent;
        box-shadow: none;
        border-bottom: 1px solid $black10-cl;
        .container {
            position: relative;
            display: flex;
            justify-content: space-between;
            align-items: center;
            .logo-wrap {
                grid-column: 1/3;
                margin-left: 0;
                z-index: 10;
            }
            .header-links-wrap {
                position: absolute;
                right: 0;
                top: -24px;
                flex-direction: column;
                display: flex;
                align-items: start;
                //min-width: 240px;
                padding: 80px 32px 48px;
                box-shadow: 0 8px 20px 0 rgba(139, 155, 178, .3);
                background-color: $white-cl;
                @include transform(translateX(100%));
                @include transition(transform $t);
                .top-line {
                    display: block;
                    width: 100%;
                    height: 1px;
                    background-color: $black10-cl;
                    margin-bottom: 24px;
                }
                .header-link {
                    margin: 8px 0;
                    font-size: 1.6rem;
                    line-height: 2.6rem;
                }
                .line {
                    display: block;
                    width: 100%;
                    opacity: 1;
                    margin: 24px 0;
                }
                .contact-wrap {
                    display: block;
                    .contact-link {
                        font-size: 1.6rem;
                        line-height: 2.6rem;
                    }  
                }
                &.show {
                    @include transform(none);
                }
            }   
            .header-contact-wrap {
                display: none;             
            }
            .header-menu-toggle {
                @include size(24px);
                z-index: 10;
                position: relative;
                cursor: pointer;
                .ic-menu-1,
                .ic-menu-2,
                .ic-menu-3 {
                    position: absolute;                    
                    display: block;
                    background-color: $black90-cl;
                    @include transition($t);
                }
                .ic-menu-1 {
                    top: 5px;
                    right: 3px;
                    width: 18px;
                    height: 2px;
                }
                .ic-menu-2 {
                    top: 11px;
                    right: 3px;
                    width: 18px;
                    height: 2px;
                }
                .ic-menu-3 {
                    top: 17px;
                    right: 3px;
                    width: 9px;
                    height: 2px;
                }
                &.show {
                    .ic-menu-1 {
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%,-50%) rotate(
                        -45deg);
                        width: 19px;
                    }
                    .ic-menu-2 {
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%,-50%) rotate(
                        45deg);
                        width: 19px;
                    }
                    .ic-menu-3 {
                        transform: scaleX(0);
                    }
                }
            }
        }
        &.scroll {
            background-color: #fff;
            box-shadow: 0 2px 4px rgba(0,0,0,.05);
        }
    } 
    @include xs {
        padding: 24px 0;
        display: block;   
        .container {
            padding: 0 20px;
            .header-links-wrap {
                min-width: auto;
                //left: 0px;
                padding-left: 24px;
                padding-right: 24px;
                padding-bottom: 32px;
                //align-items: center;
            }
        }     
    }   
}